import React from "react";
import {
  ThemeProvider,
  createTheme,
  Arwes,
  Line,
  Words,
  Frame,
  Link,
  Table,
  Blockquote
} from "@arwes/arwes";
import axios from 'axios';
import { Input } from 'antd';
import '@mdi/font/css/materialdesignicons.css'
import 'antd/dist/antd.css';



const clientHeight = window.innerHeight * 0.8
const Search = Input.Search;
class App extends React.Component {
  constructor(props) {
    super(props);
    this.onSearch=this.onSearch.bind(this);
    this.state = {
      is_visable: false,
      host: "",
      matches: [],
      web_title: "",
      web_keywords: "",
      web_desc: "",
      tip_msg:"Begin with your first url. :)"
    };

  }

  onSearch(value){
    console.log("searching:",value);
    this.setState(()=>{
      return{
        is_visable: false,
        tip_msg:'Analyzing '+value+' ...Please wait...'
      }})
    axios.post('/analyze',"url="+value)
    .then((res) =>{
      let resData=res.data;
      console.log(resData);
      if (resData.code===0){
          //请求成功处理逻辑
          let webData=JSON.parse(resData.data)
          let is_visable=true;
          let host=webData.host;
          let title=webData.web_title;
          let keywords=webData.web_keywords;
          let desc=webData.web_desc;
          let matches=[];
          for (let i = 0; i < webData.matches.length; i++) { 
            matches.push([i+1,webData.matches[i].app_name,webData.matches[i].version])
         }
         this.setState(()=>{return{
          is_visable: true,
          host: host,
          matches: matches,
          web_title: title,
          web_keywords: keywords,
          web_desc: desc,
         }})
          
      }
      else{
        this.setState(()=>{
          return{
            is_visable: false,
            tip_msg:'Request failed['+res.data.code+"], "+res.data.msg
          }})
      }
    })
    .catch((err)=> {
      console.log(err);
      this.setState(()=>{
        return{
          is_visable: false,
          tip_msg:err.toString()
        }})
    });
  }

  render() {
    return (
      <ThemeProvider theme={createTheme()}>
        <Arwes>
          <div style={{ margin: 20 }}>
            <Frame
              show={true}
              animate={true}
              level={3}
              corners={4}
              layer='primary'
            >
              <div >
                <div style={{ margin: 10 }}>
                  <Link href='https://github.com/BenDerPan/go-wappalyzer'> <i className="mdi mdi-github-circle" />Github (BenDerPan/go-wappalyzer)</Link>
                  <Search
                    placeholder="http://example.com"
                    onSearch={value => this.onSearch(value)}
                    style={{ width: 300, float: "right" }}
                  />
                </div>
                <Line animate></Line>
                <div style={{ height: clientHeight }}>
                  <div style={{ margin: 20, display: this.state.is_visable ? "none" : "block" }}>
                    <Link>{this.state.tip_msg}</Link>
                  </div>
                  <div style={{ margin: 20, display: this.state.is_visable ? "block" : "none" }}>
                    <div style={{margin:10}}>
                    <Words>Host: </Words>
                    <Link style={{margin:10}} href={this.state.host}>{this.state.host}</Link>
                    <Words>Title: </Words>
                    <Link style={{margin:10}}> {this.state.web_title}</Link>
                    </div>
                    <div>
                    <Words style={{margin:10}}>Keywords</Words>
                    <Blockquote style={{margin:10}}>{this.state.web_keywords}</Blockquote>
                    </div>
                    <div>
                    <Words style={{margin:10}}>Description</Words>
                    <Blockquote style={{margin:10}}>{this.state.web_desc}</Blockquote>
                    </div>
                    <Line  animate></Line>
                    <Table
                      animate
                      headers={['No.', 'Service', 'Version']}
                      dataset={this.state.matches}
                    />
                  </div>
                </div>
              </div>
            </Frame>

          </div>
        </Arwes>
      </ThemeProvider>
    );
  }
}

export default App;
